.selectForm {display: inline-block; height: 50px; line-height: 50px; font-size: 15px; border-radius: 4px; padding: 0 35px 0 15px; border: 1px solid #d6dade; background-image: url("/img/pbb/common/selector-down-arrow.png"); background-color: #fff; background-repeat: no-repeat; background-position-x: calc(100% - 10px); background-position-y: center; background-size: auto; font-weight: 600; color: #999da5;
    -moz-appearance: none;
    -webkit-appearance: none;
    appearance: none;
    transform: skew(-0.03deg);}
.dateForm {display: inline-block;height: 50px;line-height: 50px;font-size: 15px;border-radius: 4px;padding: 0 35px 0 15px;border: 1px solid #d6dade;background: #fff url("/img/pbb/common/date-icon.png") no-repeat calc(100% - 10px) center / 15px;font-weight: 600;color: #696d75;box-sizing: border-box;}
.textForm {display: inline-block; height: 50px; line-height: 50px; font-size: 15px; border-radius: 4px; padding-left: 15px; border: 1px solid #d6dade;box-sizing: border-box;background-color:#fff;}
.radioLabel {display:inline-block;position: relative;padding-left:30px;line-height: 22px;}
.radioForm {width: 0;height: 0;overflow: hidden;display: inline-block;text-indent: -9999px;line-height: 1px;position: absolute;}
.radioForm + .radioImg {display:inline-block;width:22px;height:22px;border:1px solid #c7cbcf;border-radius: 50%;overflow: hidden;background-color:#fff;box-sizing: border-box;position: absolute;left:0;}
.radioForm:checked + .radioImg {border:6px solid #596d93;}
.radioForm:focus + .radioImg {outline:1px solid #161616;}
.checkboxLabel {display:inline-block;position: relative;padding-left:30px;line-height: 22px;}
.checkboxForm {width: 0;height: 0;overflow: hidden;display: inline-block;text-indent: -9999px;line-height: 1px;position: absolute;}
.checkboxForm + .checkboxImg {display:inline-block;width:22px; height:22px;border:1px solid #d6dade;border-radius: 3px;overflow:hidden;background-color:#fff;position: absolute;left:0;box-sizing: border-box;}
.checkboxForm:checked + .checkboxImg {background:url('/img/pbb/common/checked_img.png') no-repeat center / auto;}
.checkboxForm:focus + .checkboxImg {outline:1px solid #161616;}
.btn {display: inline-block; font-size: 16px; border: 0; padding: 0 77px; background-color: #596d93; color: #fff; cursor: pointer; height: 50px; line-height: 50px; font-weight: 600; transition: all 0.3s ease-in-out; border-radius: 30px;border:1px solid #596d93;}
