@charset "UTF-8";
.main_wrap {
    // visual
    .visual {
        width:100%; height:calc(100vh - 120px); min-height:790px;
        @extend %clearfix;
        .visual_lnb_wrap {
            width:510px; height:100%; background-color:$blue1; padding:60px 60px 0 60px; position:relative; float:left;
            @include boxSizing;
            h3 {
                color:$white; font-weight:$bold; margin:0 0 40px;
                @include fontRul(32px);
            }
            .visual_lnb {
                width:100%; border-top:1px solid rgba(255, 255, 255, .1); padding:40px 0 0;
                li.active{
                    a{
                        font-weight:$bold;
                        @include opacity(1);
                        &:after{
                            border-color:$white;
                            @include rotate(-45);
                        }
                    }
                    div{@include opacity(1);}
                }
                a {
                    width:100%; display:block; color:$white; font-weight:$regular; padding:0 0 0 15px; word-break:break-all; margin:0 0 40px 40px;
                    position:relative; font-size:22px; line-height:28px; letter-spacing:-.5px;
                    @include opacity(.4);
                    @include transition(opacity);
                    &:after{
                        width:10px; height:10px; content:""; display:inline-block; border-bottom:2px solid $blue1; border-right:2px solid $blue1;
                        margin:0 0 0 10px;
                        @include transition(all);
                    }
                    img {
                        position:absolute; top:50%; left: -48px;
                        @include translate(0, -50%);
                    }
                }
                div {
                    width:450px; height:100%; position:absolute; top:0; left:510px; padding:40px 0 0 36px; background-color:$white;
                    @include opacity(0);
                    @include boxSizing;
                    @include transition(opacity, .8s);
                    li {
                        font-size:16px; line-height:24px; letter-spacing:.5px; color:$blue4; margin:0 0 16px;
                        span {
                            width:24px; height:24px; display:inline-block; text-align:center; background-color:$blue4; margin:0 10px 0 0;
                            color:$white; font-size:14px; font-weight:$regular; line-height:24px;
                            @include borderRadius(24px);
                        }
                    }
                }
                strong {
                    color:$blue1; font-weight:$bold; margin:0 0 30px; display:block;
                    @include fontRul(26px);
                }

            }
            .visual_lnb_indicater{
                width:100%; height:36px; position:relative;
                button{
                    width:36px; height:36px; text-align:center; display:inline-block; overflow:hidden; position:absolute;
                    span{width:10px; height:10px; display:inline-block; text-indent:-9999px; border-top:2px solid #b6babe; border-left:2px solid #b6babe; vertical-align:middle;}
                    &.btn_next{
                        top:0; right:0;
                        span{
                            @include rotate(-225);
                        }
                    }
                    &.btn_prev{
                        top:0; left:0;
                        span{
                            @include rotate(-45);
                        }
                    }
                }
                ul{
                    width:80%; height:4px; display:flex; flex-wrap:nowrap; justify-content:space-between; position:absolute; top:50%; left:50%;
                    @include translate(-50%, -50%);
                    li{
                        width:12%; height:100%; display:block; background-color:rgba(255,255,255,.3); cursor:pointer; text-align:right; position:relative;
                        button{
                            width:0; height:100%; display:inline-block; overflow:hidden; text-indent:-9999px; background-color:$white; position:absolute; top:0; left:50%; padding:0;
                            @include translate(-50%, 0);
                            @include transition(width, .2s);
                        }
                        &.active button{width:100%;}
                    }
                }
            }
        }
        .img_map{
            width:calc(100% - 960px); height:100%; float:right; position:relative; overflow:hidden; background-color:$white;
            li{width:100%; height:100%;}
            img{
                height:100%; position:absolute; top:50%; left:50%;
                @include translate(-50%, -50%);
                @include opacity(0);
                @include transition(opacity, .8s);
            }
            li.active img{
                z-index:1;
                @include opacity(1);
            }
        }
        @include pc-a(){
            /*visual  desktop-big*/
            max-width:1440px; height:calc(100vh - 110px); margin:0 auto;
            .visual_lnb_wrap {
                h3 {@include fontRul(30px);}
                .visual_lnb {
                    div {display:none;}
                }
            }
            .img_map{
                width:calc(100% - 510px);
            }
        }
        @include mo(){
            /*visual  mobile*/
            height:auto; min-height:auto;
            .visual_lnb_wrap {
                width:100%; height:100%;padding:em(80) em(60); position:relative; overflow:hidden;
                @include boxSizing;
                h3 {margin:0 0 em(60); font-size:em(48); line-height:em(60);}
                .visual_lnb {
                    padding:em(60) 0 0;
                    & > li.active a{font-weight:$regular;}
                    a {
                        padding:0 0 0 15px; word-break:break-all; margin:0 0 em(60) em(60);
                        position:relative; font-size:em(30); line-height:em(42); letter-spacing:-.5px;
                        @include opacity(1);
                        &:after{
                            width:10px; height:10px; content:""; display:inline-block; border-bottom:2px solid $white; border-right:2px solid $white;margin:0 0 0 10px;
                            @include rotate(-45);
                        }
                        img {
                            position:absolute; top:50%; left: -48px;
                            @include translate(0, -50%);
                        }
                    }
                    div {
                        width:450px; height:100%; position:absolute; top:0; left:510px; padding:40px 0 0 36px; background-color:$white;
                        @include opacity(0);
                        @include boxSizing;
                        @include transition(opacity, .8s);
                        li {
                            font-size:16px; line-height:24px; letter-spacing:.5px; color:$blue4; margin:0 0 16px;
                            span {
                                width:24px; height:24px; display:inline-block; text-align:center; background-color:$blue4; margin:0 10px 0 0;
                                color:$white; font-size:14px; font-weight:$regular; line-height:24px;
                                @include borderRadius(24px);
                            }
                        }
                    }
                    strong {
                        color:$blue1; font-weight:$bold; margin:0 0 30px; display:block;
                        @include fontRul(26px);
                    }

                }
                .visual_lnb_indicater{display:none;}
            }
            .img_map{display:none;}
        }
    }
    .life_index{
        width:1280px; padding:120px 0 0; margin:0 auto; overflow:hidden;
        & > strong{
            width:100%; display:block; color:$blue1; font-weight:$bold; text-align:center; margin:0 0 30px;
            @include fontRul(32px);
        }
        .life_index_list{
            width:100%; text-align:center; padding:0 0 700px; position:relative;
            & > li{
                display:inline-block;
                &.active > button{
                    color:$primary;
                    &:after{width:100%;}
                }
                & > button{
                    width:auto; height:30px; color:$gray2; font-weight:$bold; margin:0 12px 0;
                    @include fontRul(17px);
                    &:hover:after,
                    &:focus:after{width:100%;}
                    &:after{
                        width:0; height:4px; content:""; display:block; background-color:$primary; position:relative; top:0; left:50%;
                        @include transition(width);
                        @include translate(-50%, 0);
                    }
                }
            }
            .condition_wrap{
                width:920px; position:absolute; top:94px; left:50%; background-color:$white; display:none;
                @include translate(-50%, 0);
                @include opacity(0);
                // 공통 스타일
                .condition_info_wrap{
                    @extend %clearfix;
                    &.cdt_none{
                        width:100%; height:200px; background-color:$white; text-align:center; padding:60px 0 0;
                        @include boxSizing;
                        strong{
                            color:$blue3; font-weight:$bold;
                            @include fontRul(22px);
                        }
                        p{
                            color:$gray2; font-weight:$bold; margin:20px 0 0;
                            @include fontRul(15px)
                        }
                    }
                    .cdt_img{
                        width:330px; float:left; position:relative;
                        &>div{width:200px; height:200px; text-align:center; margin:40px; position:relative;}
                        &>div div{
                            width:70px; display:inline-block; font-size:36px; color:$white; letter-spacing:-1px; word-break:break-all;
                            position:absolute; top:50%; left:50%; font-weight:$bold;
                            @include translate(-50%, -50%);
                            span{width:200px; font-size:21px; line-height:32px; padding:14px 0 0; font-weight:$bold; white-space:nowrap; display:block; margin:0 0 0 -65px;}
                            span em{font-weight:$ebold;}
                        }
                        img{position:absolute; top:0; left:0;}
                    }
                    .condition_info{
                        width:590px; float:left; text-align:left;
                        &>ul{
                            margin:40px 0 0;
                            @extend %clearfix;
                            li{
                                width:240px; height:120px; float:left;
                                @include boxSizing;
                                &:nth-of-type(2n-1){padding:0 10px 0 0;}
                                &:nth-of-type(2n){width:350px;}

                                strong{@include fontRul(20px);}
                                p{
                                    color:$gray2; font-weight:$regular;
                                    @include fontRul(16px);
                                }
                            }
                        }
                        & > div{
                            margin:0 0 0 35px;
                            .condition_info_contents{
                                margin:40px 0 0;
                                strong{color:$blue3; font-weight:$bold; font-size:22px; letter-spacing:-1px;}
                                .data_source{
                                    display:inline-block; margin:0 0 0 5px;
                                    span{color:$gray4; font-weight:$regular; font-size:14px; letter-spacing:-.5px; display:inline-block; padding:0 12px 0;}
                                    span:first-child{border-right:1px solid $gray7;}
                                }
                            }
                            ul{
                                margin:20px 0 0;
                                li{
                                    color:$gray2; font-weight:$bold; margin:0 0 0 10px; position:relative;
                                    @include fontRul(15px);
                                    &:before{width:5px; height:1px; content:""; display:block; position:absolute; top:12px; left:-10px; background-color:$gray2;}
                                }
                            }
                        }
                    }
                }
                .condition_table{
                    margin:30px 0 0;
                    @extend %clearfix;
                    li{
                        float:left; text-align:center; margin:0 0 0 5px;
                        &:first-child{margin:0;}
                        div{
                            font-weight:$bold;
                            @include fontRul(14px);
                            &:after{width:100%; height:4px; display:block; content:"";}
                        }
                        span{
                            color:$gray4;font-weight:$bold;
                            @include fontRul(14px);
                        }
                    }
                }
                // 개별 스타일
                // 미세먼지
                &.dust_condition{
                    display:block;
                    @include opacity(1);
                    // 좋음
                    .cdt_good{
                        .cdt_img div{background-color:$cdt-blue;}
                        .condition_info li strong{ color:$cdt-blue;}
                    }
                    // 보통
                    .cdt_usually{
                        .cdt_img div{background-color:$cdt-green;}
                        .condition_info li strong{ color:$cdt-green;}
                    }
                    // 나쁨
                    .cdt_bad{
                        .cdt_img div{background-color:$cdt-orange;}
                        .condition_info li strong{ color:$cdt-orange;}
                    }
                    // 매우나쁨
                    .cdt_terrible{
                        .cdt_img div{background-color:$cdt-red;}
                        .condition_info li strong{ color:$cdt-red;}
                    }
                    .condition_table{
                        li{width:226px;}
                        li:nth-of-type(1){color:$cdt-blue;}
                        li:nth-of-type(1) div:after{background-color:$cdt-blue;}
                        li:nth-of-type(2){color:$cdt-green;}
                        li:nth-of-type(2) div:after{background-color:$cdt-green;}
                        li:nth-of-type(3){color:$cdt-orange;}
                        li:nth-of-type(3) div:after{background-color:$cdt-orange;}
                        li:nth-of-type(4){color:$cdt-red;}
                        li:nth-of-type(4) div:after{background-color:$cdt-red;}
                    }
                }
                //대기확산지수
                &.air_condition{
                    // 매우높음
                    .cdt_very_high{
                        .cdt_img div{background-color:$cdt-gray1;}
                        .cdt_img div div{color:$cdt-gray2;}
                    }
                    // 높음
                    .cdt_high{
                        .cdt_img div{background-color:$cdt-yellow1;}
                        .cdt_img div div{color:$cdt-yellow2;}
                    }
                    // 보통
                    .cdt_usually{
                        .cdt_img div{background-color:$cdt-orange;}
                    }
                    // 낮음
                    .cdt_low{
                        .cdt_img div{background-color:$cdt-red;}
                    }
                    .condition_table{
                        li{width:226px;}
                        li:nth-of-type(1){color:$cdt-gray2;}
                        li:nth-of-type(1) div:after{background-color:$cdt-gray1;}
                        li:nth-of-type(2){color:$cdt-yellow1;}
                        li:nth-of-type(2) div:after{background-color:$cdt-yellow1;}
                        li:nth-of-type(3){color:$cdt-orange;}
                        li:nth-of-type(3) div:after{background-color:$cdt-orange;}
                        li:nth-of-type(4){color:$cdt-red;}
                        li:nth-of-type(4) div:after{background-color:$cdt-red;}
                    }
                }
                //식중독지수
                &.poisoning_condition{
                    // 관심
                    .cdt_low{
                        .cdt_img div{background-color:$cdt-gray1;}
                        .cdt_img div div{color:$cdt-gray2;}
                    }
                    // 주의
                    .cdt_usually{
                        .cdt_img div{background-color:$cdt-yellow1;}
                        .cdt_img div div{color:$cdt-yellow2;}
                        .cdt_img div span{@include opacity(.5);}
                    }
                    // 경고
                    .cdt_high{
                        .cdt_img div{background-color:$cdt-orange;}
                        .cdt_img div span{@include opacity(.5);}
                    }
                    // 위험
                    .cdt_very_high{
                        .cdt_img div{background-color:$cdt-red;}
                        .cdt_img div span{@include opacity(.5);}
                    }
                    .condition_table{
                        li{width:226px;}
                        li:nth-of-type(1){color:$cdt-gray2;}
                        li:nth-of-type(1) div:after{background-color:$cdt-gray1;}
                        li:nth-of-type(2){color:$cdt-yellow1;}
                        li:nth-of-type(2) div:after{background-color:$cdt-yellow1;}
                        li:nth-of-type(3){color:$cdt-orange;}
                        li:nth-of-type(3) div:after{background-color:$cdt-orange;}
                        li:nth-of-type(4){color:$cdt-red;}
                        li:nth-of-type(4) div:after{background-color:$cdt-red;}
                    }
                }
                //체감온도
                &.temp_condition{
                    .cdt_depth{
                        width:100%; padding:0 0 em(400);
                        li{
                            display:inline-block;
                            button:after{width:1px; height:10px; content:""; display:block; position:absolute; top:13px; right:0; background-color:$gray7;}
                            button:hover,
                            button:focus {color:$primary;}
                            button{
                                width:100%; padding:5px 15px; position:relative; z-index:1; font-weight:$bold;
                                @include borderRadius(30px);
                                @include fontRul(15px);
                            }
                            &.active button{background-color:$primary; color:$white;}
                            &:first-child .condition_wrap{display:block; @include opacity(1);}
                            &.active button:after,
                            &:last-child button:after{display:none;}
                        }
                    }
                    // -
                    .cdt_low{
                        .cdt_img div{background-color:$cdt-gray1;}
                        .cdt_img div div{color:$cdt-gray2;}
                    }
                    // 관심
                    .cdt_usually{
                        .cdt_img div{background-color:$cdt-yellow1;}
                        .cdt_img div div{color:$cdt-yellow2;}
                        .cdt_img div span{@include opacity(.5);}
                    }
                    // 주의
                    .cdt_high{
                        .cdt_img div{background-color:$cdt-orange;}
                        .cdt_img div span{@include opacity(.5);}
                    }
                    // 경고
                    .cdt_very_high{
                        .cdt_img div{background-color:$cdt-red;}
                        .cdt_img div span{@include opacity(.5);}
                    }
                    // 위험
                    .cdt_danger{
                        .cdt_img div{background-color:$cdt-purple;}
                        .cdt_img div span{@include opacity(.5);}
                    }
                    .condition_table{
                        margin:60px 0 0;
                        li{width:180px;}
                        li:nth-of-type(1){color:$cdt-gray2;}
                        li:nth-of-type(1) div:after{background-color:$cdt-gray1;}
                        li:nth-of-type(2){color:$cdt-yellow1;}
                        li:nth-of-type(2) div:after{background-color:$cdt-yellow1;}
                        li:nth-of-type(3){color:$cdt-orange;}
                        li:nth-of-type(3) div:after{background-color:$cdt-orange;}
                        li:nth-of-type(4){color:$cdt-red;}
                        li:nth-of-type(4) div:after{background-color:$cdt-red;}
                        li:nth-of-type(5){color:$cdt-purple;}
                        li:nth-of-type(5) div:after{background-color:$cdt-purple;}
                    }
                }
                //자외선지수
                &.uv_condition{
                    // 낮음
                    .cdt_low{
                        .cdt_img div{background-color:$cdt-gray1;}
                        .cdt_img div div{color:$cdt-gray2;}
                    }
                    // 보통
                    .cdt_usually{
                        .cdt_img div{background-color:$cdt-yellow1;}
                        .cdt_img div div{color:$cdt-yellow2;}
                        .cdt_img div span{@include opacity(.5);}
                    }
                    // 높음
                    .cdt_high{
                        .cdt_img div{background-color:$cdt-orange;}
                        .cdt_img div span{@include opacity(.5);}
                    }
                    // 매우높음
                    .cdt_very_high{
                        .cdt_img div{background-color:$cdt-red;}
                        .cdt_img div span{@include opacity(.5);}
                    }
                    // 위험
                    .cdt_danger{
                        .cdt_img div{background-color:$cdt-purple;}
                        .cdt_img div span{@include opacity(.5);}
                    }
                    .condition_table{
                        li{width:180px;}
                        li:nth-of-type(1){color:$cdt-gray2;}
                        li:nth-of-type(1) div:after{background-color:$cdt-gray1;}
                        li:nth-of-type(2){color:$cdt-yellow1;}
                        li:nth-of-type(2) div:after{background-color:$cdt-yellow1;}
                        li:nth-of-type(3){color:$cdt-orange;}
                        li:nth-of-type(3) div:after{background-color:$cdt-orange;}
                        li:nth-of-type(4){color:$cdt-red;}
                        li:nth-of-type(4) div:after{background-color:$cdt-red;}
                        li:nth-of-type(5){color:$cdt-purple;}
                        li:nth-of-type(5) div:after{background-color:$cdt-purple;}
                    }
                }
                //감기가능지수
                &.cold_condition{
                    // 낮음
                    .cdt_low{
                        .cdt_img div{background-color:$cdt-gray1;}
                        .cdt_img div div{color:$cdt-gray2;}
                    }
                    // 보통
                    .cdt_usually{
                        .cdt_img div{background-color:$cdt-yellow1;}
                        .cdt_img div div{color:$cdt-yellow2;}
                    }
                    // 높음
                    .cdt_high{
                        .cdt_img div{background-color:$cdt-orange;}
                    }
                    // 매우높음
                    .cdt_very_high{
                        .cdt_img div{background-color:$cdt-red;}
                    }
                    .condition_table{
                        li{width:226px;}
                        li:nth-of-type(1){color:$cdt-gray2;}
                        li:nth-of-type(1) div:after{background-color:$cdt-gray1;}
                        li:nth-of-type(2){color:$cdt-yellow1;}
                        li:nth-of-type(2) div:after{background-color:$cdt-yellow1;}
                        li:nth-of-type(3){color:$cdt-orange;}
                        li:nth-of-type(3) div:after{background-color:$cdt-orange;}
                        li:nth-of-type(4){color:$cdt-red;}
                        li:nth-of-type(4) div:after{background-color:$cdt-red;}
                    }
                }
                //동파가능지수
                &.freeze_condition{
                    // 낮음
                    .cdt_low{
                        .cdt_img div{background-color:$cdt-gray1;}
                        .cdt_img div div{color:$cdt-gray2;}
                    }
                    // 보통
                    .cdt_usually{
                        .cdt_img div{background-color:$cdt-yellow1;}
                        .cdt_img div div{color:$cdt-yellow2;}
                    }
                    // 높음
                    .cdt_high{
                        .cdt_img div{background-color:$cdt-orange;}
                    }
                    // 매우높음
                    .cdt_very_high{
                        .cdt_img div{background-color:$cdt-red;}
                    }
                    .condition_table{
                        li{width:226px;}
                        li:nth-of-type(1){color:$cdt-gray2;}
                        li:nth-of-type(1) div:after{background-color:$cdt-gray1;}
                        li:nth-of-type(2){color:$cdt-yellow1;}
                        li:nth-of-type(2) div:after{background-color:$cdt-yellow1;}
                        li:nth-of-type(3){color:$cdt-orange;}
                        li:nth-of-type(3) div:after{background-color:$cdt-orange;}
                        li:nth-of-type(4){color:$cdt-red;}
                        li:nth-of-type(4) div:after{background-color:$cdt-red;}
                    }
                }
            }
        }
        @include mo(){
            /*life_index  mobile*/
            width:100%; padding:em(100) 0; overflow:hidden;
            & > strong{margin:0 0 em(30); font-size:em(40); line-height:em(52);}
            .life_index_list{
                width:100%; text-align:center; padding:0 em(30) em(900); position:relative;
                @include boxSizing;
                @include transition(all);
                & > li{
                    & > button{
                        height:em(50); margin:em(30) em(12) 0; font-size:em(26); line-height:em(42);
                        &:hover:after,
                        &:focus:after{width:100%;}
                        &:after{width:0; height:2px; content:""; display:block; background-color:$primary; position:relative; top:0; left:50%;}
                    }
                }
                .condition_wrap{
                    width:100%; position:absolute; top:em(240); left:50%; background-color:$white; display:none;
                    // 공통 스타일
                    .condition_info_wrap{
                        &.cdt_none{
                            width:100%; height:200px; background-color:$white; text-align:center; padding:60px 0 0;
                            @include boxSizing;
                            strong{
                                color:$blue3; font-weight:$bold;
                                @include fontRul(22px);
                            }
                            p{
                                color:$gray2; font-weight:$bold; margin:20px 0 0;
                                @include fontRul(15px)
                            }
                        }
                        .cdt_img{
                            width:em(240); float:none; margin:0 auto;
                            img{width:em(330);}
                            &>div{width:em(240); height:em(240); text-align:center; margin:0; position:relative; top:em(20); left:0;}
                            &>div div{
                                width:50px; display:inline-block; font-size:em(40); color:$white;
                                position:absolute; top:50%; left:50%; font-weight:$bold;
                                span{width:em(240); font-size:em(36); line-height:32px; padding:em(14) 0 0; margin:0 0 0 em(-70);}
                                span em{font-weight:$ebold;}
                            }
                            img{position:absolute; top:0; left:0;}
                        }
                        .condition_info{
                            width:100%; float:none; text-align:center;
                            &>ul{
