                                }
                                &.active button{background-color:$primary; color:$white;}
                                &:first-child .condition_wrap{display:block; @include opacity(1);}
                                &.active button:after,
                                &:last-child button:after{display:none;}
                            }
                        }
                        .condition_table{margin:em(40) 0 0;}
                        .condition_table li{width:calc(20% - 5px);}
                    }
                    //자외선지수
                    &.uv_condition{
                        .condition_table li{ width:calc(20% - 5px);}
                    }
                    //감기가능지수
                    &.cold_condition{
                        .condition_table li{ width:calc(25% - 5px);}
                    }
                    //동파가능지수
                    &.freeze_condition{
                        .condition_table li{ width:calc(25% - 5px);}
                    }
                }
            }
        }
