    .bucheon_communicate{
        width:100%; height:990px; background-color:$bg-gray2; overflow:hidden;
        & > div{
            width:1280px; height:100%; margin:0 auto; padding:230px 0 0;
            @include boxSizing;
            & > strong{
                display:block; color:$blue1; font-weight:$bold; text-align:center;
                @include fontRul(32px);
            }
            .bucheon_communicate_slick{
                width:1240px;margin:96px auto 0; padding:0 0 20px;
                @extend %clearfix;
                .communi_list{
                    width:295px; height:295px; background-color:$white; float:left; margin:0 10px 0; padding:30px; border:1px solid $gray6;
                    @include boxSizing;
                    &:last-child{margin:0;}
                    &:hover,
                    &:focus{@include boxShadow(5px 5px 10px $gray7);}
                }
                a{width:100%; height:100%; display:block;}
                a:hover strong,
                a:focus strong{text-decoration:underline;}
                .cont_label{
                    display:inline-block; padding:2px 15px; color:$white; font-weight:$bold; background-color:$blue4;
                    @include fontRul(16px);
                    @include borderRadius(30px);
                }
                strong{
                    width:200px; height:60px; display:block; color:$blue3; font-weight:$bold; font-size:22px; letter-spacing:-1px; line-height:30px; margin:24px 0 14px;
                    @include multi-text-overflow(2);
                }
                p{
                    width:220px; height:40px; display:block; color:$gray2; font-weight:$regular; font-size:15px; letter-spacing:-.5px; line-height:20px;
                    @include multi-text-overflow(2);
                }
                .belong_wrap{
                    color:$gray4; font-weight:$regular; margin:40px 0 0;
                    @include fontRul(14px);
                    span{display:inline-block; margin:0 10px 0 0;}
                    span:last-child{margin:0;}
                }
            }
            .communicate_slick_ui{display:none;}
        }
        @include mo(){
            /*bucheon_festival  mobile*/
            width:100%; height:em(990);
            & > div{
                width:100%; padding:em(100) 0 0;
                @include boxSizing;
                & > strong{font-size:em(40);}
                .bucheon_communicate_slick{
                    width:100%; margin:em(100) auto 0; padding:0 0 em(20);
                    .communi_list{
                        width:em(350); height:auto; margin:0 em(10) 0; padding:em(30);
                    }
                    .cont_label{
                        display:inline-block; padding:em(8) em(30); font-size:em(22); line-height:em(36);
                    }
                    strong{
                        width:100%; height:em(80); font-size:em(28); letter-spacing:-.5px; line-height:em(40); margin:em(20) 0;
                    }
                    p{
                        width:100%; height:em(60); font-size:em(22); letter-spacing:0; line-height:em(34);
                    }
                    .belong_wrap{
                        margin:em(40) 0 0; font-size:em(22); letter-spacing:0; line-height:em(28);
                        span{display:block; margin:0;}
                        span:last-child{margin:0;}
                    }
                }
                .communicate_slick_ui{
                    width:em(360); height:em(44); margin:em(30) auto 0; text-align:center; display:block;
                    .communicate_indicater{
                        width:100%; height:2px; display:inline-block; position:relative; background-color:$gray6; vertical-align:middle;
                        div{width:em(90); height:100%; background-color:$blue2; position:absolute; overflow:hidden; text-indent:-9999px;}
                    }
                    .communicate_index_wrap{
                        display:inline-block; vertical-align:middle; margin:0;
                        span{
                            width:em(26); display:inline-block; color:$gray2; font-weight:$bold; font-size:em(22);
                        }
                    }
                }
            }
        }
    }
    /*alert*/
    #pageAlert{
