@charset "UTF-8";
/* wrap */
#wrap{
    min-width:1280px;
    @include mo(){
        min-width:100%;
    }
}
/* skip nav */
.go_active_nav,
.go_active_content {
    position:fixed; top:-100%; left:0; width:100%; height:40px; display:block;color:$white; background-color:$black;
    text-align:center; line-height:40px; font-weight:$bold;
    &:focus {top:0;}
}
.go_active_nav:focus + .go_active_content + #globalHeader {top:40px !important;}
.go_active_content:focus + #globalHeader {top:40px !important;}
/* header */
#globalHeader {
    width:100%;position:fixed; top:0; left:0; z-index:999; background-color:$white;
    .header_wrap {
        width:100%; height:120px; padding:30px 50px 20px; border-bottom:1px solid #d6dade;
        @include boxSizing;
        h1 {display:block; position:absolute; top:30px; left:50px;}
        #globalNav {
            width:1280px; height:70px;margin:0 auto;
            .close_btn {display:none}
            .gnb_wrap {
                @extend %clearfix;
                & > li {
                    height:70px; margin:0 0 0 58px; float:left;
                    &:first-child {margin:0;}
                    & > a {
                        width:100%;height:100%; display:block; position:relative; top:0; left:0;
                        font-size:18px; font-weight:$bold; letter-spacing:-.5px; line-height:70px;
                        &:hover:after ,
                        &:focus:after,
                        &.open:after {width:100%;}
                        &:after {
                            width:0; height:4px; display:block; content:""; background-color:$primary; margin:-11px auto 0;
                            @include transition(width);
                        }
                    }
                }
                .depth_wrap {
                    width:100%; overflow:hidden; position:absolute; top:120px; left:0; background-color:$white;
                    @include boxShadow(0 -1px 1px 0 #d6dade inset);
                    .depth_list {
                        width:1280px; margin:0 auto; padding:60px 0;
                        @extend %clearfix;
                    }
                    .depth2 {
                        width:170px; margin:0 0 0 40px; float:left; background-color:$white;
                        &:hover > a:after,
                        &:focus >a:after {width:100%;}
                        & > a {
                            width:100%; height:auto; display:block; position:relative; padding:0 0 15px; color:$blue3; font-weight:$bold;
                            @include fontRul(18px);
                            &:hover:after,
                            &:focus:after {width:100%;}
                            &:before {width:100%; height:2px; display:block; content:""; background-color:#e6eaee; position:absolute; bottom:0; left:0; z-index:0;}
                            &:after {
                                width:0; height:2px; display:block; content:""; background-color:$primary;
                                position:absolute; bottom:0; left:0; z-index:1;
                                @include transition();
                            }
                        }
                    }
                    .depth3 {
                        margin:18px 0 0;
                        li {
                            width:100%;
                            a {
                                &:hover,
                                &:focus,
                                &.active { color:$primary; }
                                width:100%; font-weight:$regular;
                                @include fontRul(15px);
                            }
                        }
                    }
                }
            }
        }
        .gnb_utility {
            height:120px; position:absolute; top:0; right:50px;
            @extend %clearfix;
            li {
                width:28px; height:28px; float:left; position:relative; top:53%; margin:0 20px 0 0;
                @include translate(0, -50%);
                a, button { width:100%; height:100%; display:block; text-indent:-9999px;}
                &:last-child {margin:0;}
                &.user_rank_open {
                    button {
                        background-image:url("/img/cs/layout/icon_rank.jpg"); background-size:cover;
                        &:hover,
                        &:focus {background-image:url("/img/cs/layout/icon_rank_on.jpg");}
                    }
                }
                &.my_page {
                    a {
                        background-image:url("/img/cs/layout/icon_mypage.jpg"); background-size:cover;
                        &:hover,
                        &:focus {background-image:url("/img/cs/layout/icon_mypage_on.jpg");}
                    }
                }
                &.site_map_open {
                    button {
                        background-image:url("/img/cs/layout/icon_list.gif"); background-size:cover;
                        &:hover,
                        &:focus {background-image:url("/img/cs/layout/icon_list_on.gif");}
                    }
                }
            }
        }
        @include pc-a() {
            /*header_wrap  desktop-big*/
            width:100%; height:110px; padding:20px 50px 15px;
            h1 {top:20px; left:50px;}
            #globalNav {
                .gnb_wrap {display:none;}
            }
            .gnb_utility {
                height:110px;
                li {top:50%;}
            }
        }
        //1280
        @include mo() {
            /*header_wrap  mobile*/
            width:100%; height:em(140); padding:0 em(30);
            h1 {
                top:em(30); left:em(30);
                img {height:em(76);}
            }
            #globalNav {
                width:100%; height:100vh; background-color:$white; position:fixed; top:0; left:0; z-index:9999; overflow:scroll; display:none;
                @include opacity(0);
                .close_btn {
                    width:em(48); height:em(48); display:block; position:fixed; z-index:10; top:em(30); right:em(32); text-indent:-9999px; background-color:$white;
                    &:before {
                        width:em(48); height:2px; display:block; content:""; background-color:#7d8088; position:absolute; top:em(24); left:0;
                        @include rotate(45);
                    }
                    &:after {
                        width:em(48); height:2px; display:block; content:""; background-color:#7d8088; position:absolute; top:em(24); left:0;
                        @include rotate(-45);
                    }
                }
                .gnb_wrap {
                    width:em(300); height:100%; display:block; background-color:$bg-gray2; padding:em(100) 0 0;
                    @include boxSizing;
                    & > li {
                        width:em(280); height:auto; margin:0 em(48); float:none;
                        &:first-child {margin:0 em(48) 0;}
                        & > a {
                            width:em(200);position:relative; top:0; left:0; padding:em(36) 0; font-size:em(30); font-weight:bold; line-height:em(40); letter-spacing:-1px; text-align:left;
                            &:hover:after,
                            &:focus:after {width:0;}
                            &:after {
                                width:0; height:4px; display:block; content:""; background-color:$primary; margin:-11px auto 0;
                                @include transition(width);
                            }
                            &.open {
                                color:$primary;
                                &:after {width:0;}
                            }
                        }
                        .depth_wrap {
                            width:70%; height:100vh; overflow:scroll; position:absolute; top:0; left:em(300); background-color:$white; box-shadow:none; display:none;
                            @include opacity(0);
                            .depth_list {width:100%; margin:0 auto; padding:em(100) 0;}
                            .depth2 {
                                width:em(300); margin:em(40) 0 em(40) em(40); float:none; background-color:$white;
                                &:hover > a:after,
                                &:focus > a:after {width:100%;}
                                & > a {
                                    width:100%; height:auto; display:block; position:relative; padding:0 0 15px; color:$blue3; font-weight:$bold;
                                    font-size:em(32);
                                    &:before {
                                        width:100%; height:2px; display:block; content:""; background-color:#e6eaee;
                                        position:absolute; bottom:0; left:0; z-index:0;
                                    }
                                    &:after {
                                        width:0; height:2px; display:block; content:""; background-color:$primary;
                                        position:absolute; bottom:0; left:0; z-index:1;
                                        @include transition();
                                    }
                                }
                            }
                            .depth3 {
                                margin:18px 0 0;
                                li {
                                    width:100%;
                                    a {
                                        width:100%; font-weight:$regular; font-size:em(26);
                                        &:hover,
                                        &:focus,
                                        &.active { color:$primary; }
                                    }
                                }
                            }
                        }
                    }
                }
            }
            .gnb_utility {
                height:em(140); right:em(30);
                li {
                    width:em(50); height:em(50);
                }
            }
        }
    }
    .site_map_wrap {
        width:100vw; height:100vh; position:fixed; top:0; left:0; z-index:9999; background-color:$white; overflow-y:scroll; display:none;
        @include opacity(0);
        .map_header {
            width:100%; height:190px; position:fixed; top:0; left:-8px; z-index:5; border-bottom:1px solid #d6dade; background-color:$white;
            & > div {
                width:100%; height:120px; background-color:$blue1;
                div {width:1280px; height:100%; margin:0 auto; text-align:center; line-height:120px; position:relative;}
                h2 {
                    display:inline; color:$white;
                    @include fontRul(36px);
                }
                button {
                    width:34px; height:34px; display:block; position:absolute; top:43px; right:0; text-indent:-9999px;
                    &:before {
                        width:34px; height:2px; display:block; content:""; background-color:$white; position:absolute; top:16px; left:0;
                        @include rotate(45);
                    }
                    &:after {
                        width:34px; height:2px; display:block; content:""; background-color:$white; position:absolute; top:16px; left:0;
                        @include rotate(-45);
                    }
                }
            }
            ul {
                width:1280px; height:70px; margin:0 auto; display:flex; flex-direction:row; justify-content:space-between;
                li {
                    height:100%; line-height:70px;
                    button {
                        color:$gray1; font-weight:$bold; position:relative;
                        @include fontRul(18px);
                        &:after {
                            width:0; height:4px; content:""; display:block; background-color:$primary; margin:0 auto; position:relative; top:18px;
                            @include transition(width);
                        }
                    }
                    &.active {
                        button {
                            color:$primary;
                            &:after {width:100%; position:relative; top:18px; left:0;}
                        }
                    }
                }
            }
        }
        .map_list_wrap {
            width:100%; height:auto; padding:190px 0 0;
            & > ul {
                width:1280px; margin:0 auto;
                & > li {
                    width:100%; padding:80px 0 60px; margin:0 20px; border-bottom:1px solid $gray6;
                    @include boxSizing();
                    @extend %clearfix;
                    &:last-child {border-bottom:none};
                    & > a {
                        display:block; width:214px; margin:0 0 0 80px; float:left; color:$gray1; font-weight:$bold;
                        @include fontRul(30px);
                    }
                    .map_depth_wrap {
                        width:820px; margin:0 0 0 124px; float:left;
                        .map_depth_list {
                            display:flex; flex-direction:row; flex-wrap:wrap;
                        }
                        .depth2 {
                            width:25%; margin:0 0 40px;
                            & > a {
                                width:100%; display:block; font-weight:$bold; color:$blue3; padding:0 0 15px; position:relative;
                                @include fontRul(18px);
                                &:before {
                                    width:85%; height:2px; display:block; content:""; background-color:#e6eaee;
                                    position:absolute; bottom:0; left:0; z-index:0;
                                }
                                &:after {
                                    width:0; height:2px; display:block; content:""; background-color:$primary;
                                    position:absolute; bottom:0; left:0; z-index:1;
                                    @include transition();
                                }
                                &:hover:after,
                                &:focus:after {width:85%;}
                            }
                        }
                        .depth3 {
                            margin:18px 0 0;
                            li {
                                width:100%;
                                a {
                                    width:100%; font-weight:$regular; position:relative; left:0;
                                    @include fontRul(15px);
                                    @include transition(all);
                                    &.active,
                                    &:hover,
                                    &:focus {color:$primary; left:10px;}
                                }
                            }
                        }
                    }
                }
            }
        }
    }
    .user_rank_wrap {
        width:468px; height:100vh; background-color:$gray1; position:fixed; top:0; right:-500px; z-index:9999; padding:70px 60px 0; overflow:auto;
        @include boxSizing;
        @include boxShadow(10px 4px 20px #191d25);
        button.close_btn {
            width:34px; height:34px; display:block; position:absolute; top:25px; right:25px; text-indent:-9999px;
            &:before {
                width:34px; height:2px; display:block; content:""; background-color:$white; position:absolute; top:16px; left:0;
                @include rotate(45);
            }
            &:after {
                width:34px; height:2px; display:block; content:""; background-color:$white; position:absolute; top:16px; left:0;
                @include rotate(-45);
            }
        }
        .rank_title {
            position:relative; padding:0 0 24px; border-bottom:1px solid $blue3;
            h3 {
                color:$bg-gray2; font-weight:$bold;
                @include fontRul(32px)
            }
            & > button {
                width:20px; height:20px; line-height:22px; background-color:$gray6; font-size:14px; color:$gray1;
                text-align:center; font-weight:$ebold; position:absolute; top:12px; right:0;
                @include borderRadius(20px);
            }
        }
        .user_top3 {
            padding:40px 0; border-bottom:1px solid $blue3;
            & > li {
                margin:0 0 30px;
                @extend %clearfix;
                &:last-child {margin:0;}
                & > span {
                    width:60px; float:left; display:block; text-align:center; font-weight:$ebold; color:$white;
                    @include fontRul(18px);
                }
                img {float:left;}
                div {
                    width:200px; float:left; padding:0 20px 0;
                    @include boxSizing();
                    strong {
                        color:$white; font-weight:$bold;
                        @include fontRul(20px);
                    }
                    ul {
                        @extend %clearfix;
                        li {
                            width:50%; float:left; color:#99b4e2; font-weight:$regular;
                            @include fontRul(15px);
                        }
                    }
                }
            }
        }
        .user_ranker {
            padding:40px 0 0;
            & > li {
                @extend %clearfix;
                &:last-child {margin:0;}
                & > span {
                    width:60px; height:60px; float:left; display:block; text-align:center; font-weight:$ebold; color:$gray4;
                    font-size:18px; line-height:60px; letter-spacing:-.5px;
                }
                img {float:left;}
                div {
                    width:200px; float:left; padding:0 10px 0;
                    @include boxSizing;
                    strong {color:$white; font-weight:$regular; font-size:18px; line-height:60px; letter-spacing:-.5px;}
                }
            }
        }
        //1280
        @include mo() {
            width:100%; height:100vh; background-color:$gray1; position:fixed; top:0; right:-100%; z-index:9999; padding:em(100) em(60) em(60); overflow:auto;
            @include boxSizing;
            @include boxShadow(none);
            button.close_btn {
                width:em(48); height:em(48); display:block; position:absolute; top:em(30); right:em(30); text-indent:-9999px;
                &:before {
                    width:em(48); height:2px; top:em(24); left:0;
                    @include rotate(45);
                }
                &:after {
                    width:em(48); height:2px; top:em(24); left:0;
                    @include rotate(-45);
                }
            }
            .rank_title {
                padding:0 0 em(40);
                h3 {
                    font-size:em(46);
                }
            }
            .user_top3 {
                padding:em(60) 0;
                & > li {
                    margin:0 0 em(40);
                    &:last-child {margin:0;}
                    & > span {width:em(80); font-size:em(32);}
                    img {float:left; width:em(100);}
                    div {
                        width:em(300); float:left; padding:0 em(20) 0;
                        strong {font-size:em(38);}
                        ul {
                            li {font-size:em(26);}
                        }
                    }
                }
            }
            .user_ranker {
                padding:em(60) 0 0;
                & > li {
                    &:last-child {margin:0;}
                    & > span {width:em(100); height:em(100); font-size:em(32); line-height:em(100);}
                    img {float:left; width:em(100);}
                    div {
                        width:em(300); float:left; padding:0 em(20) 0;
                        @include boxSizing();
                        strong {color:$white; font-weight:$regular; font-size:18px; line-height:60px; letter-spacing:-.5px;}
                    }
                }
            }
        }
    }
}
/* footer */
#footer_wrap {
    width:100%; height:180px; background-color:#191d25;
    ul {
        width:946px; height:67px; line-height:67px; margin:0 auto; display:flex; flex-direction:row;
        li:first-child {display:none;}
        a {
            display:inline-block; margin:0 25px 0; color:$gray6; font-weight:$regular;
            @include fontRul(17px);
        }
    }
    & > div {
        width:100%; height:113px;border-top:1px solid #2b2f36; padding:0 60px 0;
        @include boxSizing;
        @extend %clearfix;
        .footer_logo {
            line-height:112px;
            img {vertical-align:middle;}
        }
        .footer_info {
            display:inline-block; vertical-align:middle; margin:0 0 0 40px;
            p {
                color:$gray4; font-weight:$bold;
                @include fontRul(14px);
                span {display:inline-block; padding:0 8px 0 0;}
            }
        }
        .copyright {height:100%; float:right; color:$gray4; font-weight:$light; font-size:14px; line-height:112px; letter-spacing:-.5px;}
    }
    @include mo() {
        height:em(380); position:relative;
        ul {
            width:em(258); height:em(64); display:block; position:absolute; top:em(52); right:em(15); border:1px solid $gray4; overflow:hidden;
            @include boxSizing;
            @include transition(height);
            li {
                width:100%; height:100%; color:$gray6; font-size:em(22); line-height:em(64); letter-spacing:-.5px; font-weight:$bold;
                @include boxSizing();
                &:first-child {display:list-item; padding:0 0 0 em(30);}
                .close_btn {
                    width:em(20); height:em(20); display:block; position:absolute; top:em(30); right:em(32); text-indent:-9999px;
                    &:before {
                        width:em(20); height:2px; display:block; content:""; background-color:$white; position:absolute; top:0; left:0;
                    }
                    &:after {
                        width:em(20); height:2px; display:block; content:""; background-color:$white; position:absolute; top:0; left:0;
                        @include rotate(-90);
                    }
                }
            }
            a {
                width:100%; height:100%; display:block; margin:0; color:$gray2; font-size:em(22);line-height:em(64); letter-spacing:-.5px; font-weight:$bold; padding:0 0 0 em(30);
                @include boxSizing();
            }
            &.active {
                width:em(258); height:em(542); display:block; position:absolute; top:em(-190); right:em(15); border:1px solid $gray4;background-color:$white;
                li {
                    width:100%; height:em(64); color:$gray2;
                    &:first-child {
                        position:relative; margin:em(10) 0;
                        &:after {
                            width:80%; height:1px; content:""; display:block; position:absolute; bottom:0; left:50%; background-color:$gray6;
                            @include translate(-50%, 0);
                        }
                    }
                }
                .close_btn {
                    &:before {
                        background-color:$gray2;
                        @include rotate(45);
                    }
                    &:after {
                        background-color:$gray2;
                        @include rotate(-45);
                    }
                }
            }
        }
        & > div {
            width:100%; height:em(170); border-top:none; padding:0 em(15) 0;
            @include boxSizing;
            .footer_logo {
                line-height:em(170);
                img {width:em(248);}
            }
            .footer_info {
                width:100%; height:auto; display:block; vertical-align:top; margin:0; border-top:1px solid #2b2f36; padding:em(34) 0 0;
                p {
                    color:$gray4; font-weight:$bold; font-size:em(22); line-height:em(38); letter-spacing:-1px;
                    span {display:inline-block; padding:0 8px 0 0;}
                }
            }
            .copyright {height:auto; float:none; color:$gray4; font-weight:$light; font-size:em(22); line-height:em(34); letter-spacing:-.5px; margin:em(34) 0 0;}
        }
    }
}

